import { BreadcrumbProps } from '@/components/breadcrumb/Breadcrumb.interface';
import { FunctionalIcon } from '@/components/icon/Icon';
import clsx from 'clsx';
import { FC, useState } from 'react';
import { focusRing } from '../../tailwind/styles/focus';

export const Breadcrumb: FC<BreadcrumbProps> = ({ items, ...props }) => {
  const [visible, setVisible] = useState<boolean>(false);
  /**
   * Add "more button" to the breadcrumb
   * if there are more than 2 items
   */
  const copiedItems =
    items.length > 2
      ? [
          ...items.slice(0, 1),
          <button
            className={clsx(
              focusRing,
              'zep-bg-greyscale-300',
              'hover:zep-bg-greyscale-400',
              'zep-rounded-4',
              'zep-flex',
              'zep-px-0.625',
              'zep-leading-[22px]',
              'zep-cursor-pointer',
            )}
            onClick={() => setVisible(true)}
          >
            ...
          </button>,
          ...items.slice(1),
        ]
      : items;

  /**
   * Show "more button" if there are more than 2 items
   * and the current index is 1
   * which is the second item
   */
  const showMoreButton = (index: number): boolean => items.length > 2 && index === 1;

  return (
    <nav className="zep-flex">
      <ol
        data-testid="zep-breadcrumb"
        aria-label="breadcrumb"
        aria-labelledby="breadcrumb"
        {...props}
        className={clsx(
          'zep-flex',
          'zep-flex-row',
          'zep-flex-wrap',
          'zep-gap-0.25',
          { 'max-sm:[&>li:not(:first-child)]:zep-hidden': !visible },
          { 'max-sm:[&>li:not(:last-child)]:zep-hidden': !visible },
        )}
      >
        {copiedItems.map((item, index) => (
          <li
            key={index}
            className={clsx(
              { 'max-sm:!zep-flex': showMoreButton(index) },
              { 'sm:!zep-hidden': showMoreButton(index) },
              { 'max-sm:!zep-hidden': showMoreButton(index) && visible },
              'last:!zep-flex',
              'first:!zep-flex',
              '[&_svg]:first:zep-hidden',
              'zep-flex',
              'zep-flex-row',
              'zep-flex-nowrap',
              'zep-items-center',
              'zep-gap-0.25',
              '[&_a]:zep-h-full',
              '[&_a]:zep-no-underline',
              '[&_a]:last:zep-font-900',
              '[&:not(:last-child)_a]:hover:zep-text-neutral-dark-hover',
              '[&:not(:last-child)_a]:hover:zep-underline',
              '[&:not(:last-child)_a]:hover:zep-decoration-2',
              '[&:last-child_a]:zep-cursor-default',
            )}
          >
            <FunctionalIcon name="chevron-mini-right" />
            {item}
          </li>
        ))}
      </ol>
    </nav>
  );
};
